.app-sidenav{
    width: 250px;
    height: 100vh;
    background-color: white;
    padding: 11px;
    padding-top: 20px;
    overflow: auto;
    z-index: 2000;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
@media(max-width: 1690px) {
    .app-sidenav{
        position: fixed;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(94, 130, 146, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }
}
.app-sidenav-container{
    margin: 40px 0px;
}
.app-sidenav-avatar-logout{
    display: flex;
    justify-content: space-between;
    align-items: center;
}