.app {
  display: flex;
  background-color: #f0f0f0;
}
.app-right {
  width: 100%;
}
.app-content-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.component-container {
  width: 600px;
  margin: 40px 20px;
}
.component-container__sm {
  width: 350px;
  margin: 40px 20px;
}
@media (max-width: 625px) {
  .component-container {
    width: 100%;
  }
}
@media (max-width: 1418px) {
  .left {
    display: none;
  }
}
@media (max-width: 1040px) {
  .right {
    display: none;
  }
}
